<template>
  <div class="mine-about-me">
    <mt-header title="关于我们" class="boder-header">
      <router-link to="/mine" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="item-container-1">
      <img class="app-icon" src="/static/images/logo.png">
      <div class="app-name">{{app_name}}</div>
    </div>

    <div class="item-container-2">
      <div class="item-style">
        <div class="item-name">官方微信</div>
        <div class="item-content">财喵万卡</div>
      </div>
      <div class="space-line"></div>
      <div class="item-style">
        <div class="item-name">客服热线</div>
        <div class="item-content">{{service_number}}</div>
      </div>
      <div class="space-line"></div>
      <div class="item-style">
        <div class="item-name">检查更新</div>
        <div class="item-content">v1.0.0</div>
      </div>
      <div class="space-line"></div>
    </div>
    <div class="item-container-3">
      <div class="tips-style">{{app_name}}</div>
      <div class="tips-style">Copyright © 2017-2018 caimiaowanka.com</div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        app_name:"财喵万卡",
        service_number:"400-900-1033",
      };
    },

  }
</script>

<style lang="postcss" scoped>
  .mine-about-me {
    background: #fbfbfb;
    height:100%;

  &  .item-container-1 {
    display: flex;
    align-items: center;
    flex-direction: column;

  &  .app-icon {
    margin-top: 13px;
    max-width: 80px;
    max-height: 80px;
  }

  &  .app-name {
    margin-top: 2px;
  }

  }
  &  .item-container-2 {
    margin-top: 22px;
    background: #ffffff;

  &  .item-style {
    height: 64px;
    min-height: 64px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

  &  .item-name {
    font-size: 16px;
    color: #1c2042;
    margin-left: 27px;
  }

  &  .item-content {
    margin-right: 18px;
    color:#4477dd;
  }

  }
  & .space-line {
      height: 1px;
      margin-left: 27px;
      background: #ececec;
    }
  }
  & .item-container-3{
    display: flex;
    align-items: center;
    padding-top: 200px;
    flex-direction: column;

    & .tips-style{
    font-size: 10px;
    color:#B9BABF;
      }
    }

  }
</style>



